html {
  font-size: 62.5%; }

body {
  background-color: #f4f3ed;
  font: 14px 'Lato', sans-serif; }

.img-size {
  border: 6px solid #dbdbdb;
  width: 29rem; }
.img-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3rem 2rem; }

.first-product {
  grid-area: 1/ 2/ 3/ 3; }
  .first-product .img-size {
    border: 6px solid #dbdbdb;
    width: 54.8rem; }

.left-products {
  justify-self: end; }

.order {
  -ms-flex-order: 1;
      order: 1; }

.product {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content; }
  .product-info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    text-transform: uppercase;
    padding-top: 1rem;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-bottom: 3rem; }
    .product-info button {
      background-color: #efb784;
      color: white;
      border: 0;
      text-transform: uppercase;
      padding: 6px 12px;
      font: 12px 'Lato', sans-serif; }
  .product div > p {
    margin: 0; }
    .product div > p:last-of-type {
      margin-top: 2px; }

.horizontal-ruler {
  background-color: #dbdbdb;
  height: 8px;
  width: 90%;
  margin: 3rem auto; }

.shopping-cart {
  text-align: center; }
  .shopping-cart-menu {
    position: relative;
    width: 70%;
    margin: 0 auto; }
  .shopping-cart-header {
    text-align: left;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr;
    border: 2px solid #dbdbdb; }
    .shopping-cart-header > span {
      padding: 1rem 0; }
      .shopping-cart-header > span:nth-child(1) {
        text-align: center; }
  .shopping-cart-items {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr;
    -ms-flex-align: center;
        align-items: center;
    justify-items: flex-start;
    border: 2px #dbdbdb;
    border-style: none solid solid solid; }
  .shopping-cart-cancel {
    font-size: 20px;
    cursor: pointer; }
  .shopping-cart-img {
    border: 2px solid #dbdbdb;
    width: 83px;
    height: 83px;
    margin: 1rem 1.5rem; }
  .shopping-cart-product {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    justify-self: center; }
  .shopping-cart-total {
    border: 2px solid #dbdbdb;
    border-style: none solid solid solid;
    padding: 1rem 0;
    margin-bottom: 4rem;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr;
    grid-auto-flow: row;
    text-align: left; }

.total-price {
  grid-column: 4/5; }

.item-input-qty {
  left: 60%;
  width: 5rem;
  text-align: center;
  padding: 0.5rem 1rem; }



